html {
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  perspective: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

html, body {
  width: 100vw;
  min-width: 100vw;
  max-width: 100vw;
  overflow: hidden;
  overscroll-behavior: none;
}

body {
  font-size: 15px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  user-select: none;
}

img {
  pointer-events: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button:not(disabled) {
  /* opacity: 0.35; */
}

.opponentZone {
  grid-template-areas:
    'slot4 slot3' 
    'slot1 slot2';
}

.opponentZone > * { height: 3.5em; width: 2.75em; }
.opponentZone > *:nth-child(1) { grid-area: slot1; }
.opponentZone > *:nth-child(2) { grid-area: slot2; }
.opponentZone > *:nth-child(3) { grid-area: slot3; }
.opponentZone > *:nth-child(4) { grid-area: slot4; }
